export default {
    data() {
        return {
            mm:'',mm1:''
        }
    },
    components: {
        
    },
    beforeCreate(){
        
    },
    computed:{
        
    },
    created(){
        document.body.scrollTop = 0
        document.documentElement.scrollTop = 0
        document.title = '支付密码'
        
    },
    methods:{
        set() {
            if(this.isNumber(this.mm1)){
                if(this.mm === this.mm1){
                    this.$store.dispatch('paypwd', this.mm1)
                }else{
                    this.$dialog.alert({
                        title: '两次输入的密码不一致!',
                        message: '',
                        confirmButtonText: '关闭'
                    }).then(() => {
                        
                    })
                }
            }else{
                this.$dialog.alert({
                    title: '密码必须为数字!',
                    message: '',
                    confirmButtonText: '关闭'
                }).then(() => {
                        
                })
            }
            // if ($(':password')[0].value != $(':password')[1].value) {
            //     this.$dialog.alert({
            //         title: '两次输入的密码不一致!',
            //         message: '',
            //         confirmButtonText: '关闭'
            //     }).then(() => {
            //     // on close
            //     });
            // }
        },
        isNumber(val) {
            var regPos = /^\d+(\.\d+)?$/; //非负浮点数
            var regNeg = /^(-(([0-9]+\.[0-9]*[1-9][0-9]*)|([0-9]*[1-9][0-9]*\.[0-9]+)|([0-9]*[1-9][0-9]*)))$/; //负浮点数
            if(regPos.test(val) || regNeg.test(val)) {
                return true;
            } else {
                return false;
            }
        }
    },
}



